import vision.gears.webglmath.*
import kotlin.math.exp
import kotlin.math.PI
import kotlin.math.floor
import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.events.*
import org.w3c.dom.Window

open class PhysicsGameObject(
  vararg meshes : Mesh
   ) : GameObject(*meshes) {

  var health = 1
  var damage = 0
  var speedBoost = 0
  var bullets = 1
  var alive = true
  var name = ""

  val velocity = Vec3 ()
  val acceleration = Vec3 ()
  val force = Vec3 ()

  var angularVelocity = 0.0f
  var angularAcceleration = 0.0f
  var torque = 0.0f

  var radius = 1.0f
  var invMass = 1.0f
  var invAngularMass = 1.0f

  open fun control (
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>) : Boolean
  {
    return true;
  }

  fun collision (
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>) : Boolean
  {

    gameObjects.forEach {
      if (it is PhysicsGameObject) {
        if (health <= 0){
          alive = false
        }
        if (it.health <= 0){
          it.alive = false
        }

        if (it != this 
          && (name != "justin" || it.name != "missile") && (name != "missile" || it.name != "justin")
          && (name != "ritvik" || it.name != "milk") && (name != "milk" || it.name != "ritvik")
          && (name != "ramsey" || it.name != "milk") && (name != "milk" || it.name != "ramsey")
          && (name != "ramsey" || it.name != "cheese") && (name != "cheese" || it.name != "ramsey")
          && (name != "ritvik" || it.name != "cheese") && (name != "cheese" || it.name != "ritvik")) {
          val diff = position - it.position
          val dist = diff.length()
          if (dist < radius + it.radius) {
            if (name != it.name){
              if (name != "milk" || it.name != "justin"){
                health -= it.damage
              }
              if (it.name != "milk" || name != "justin"){
                it.health -= damage
              }

              if (it.name == "ramsey" && name == "justin"){
                 speedBoost += 1
              }
              if (name == "ramsey" && it.name == "justin"){
                 it.speedBoost += 1
              }

              if (it.name == "ritvik" && name == "justin"){
                 bullets += 1
              }
              if (name == "ritvik" && it.name == "justin"){
                 it.bullets += 1
              }

              if (health <= 0 && name == "justin"){
                val overlay = document.getElementById("overlay") as HTMLDivElement
                overlay.innerHTML = """<font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 10px; left: 15px;">YOU WERE MILKED<br>Reload the page to try again.</font>"""
              }
              if (it.health <= 0 && it.name == "justin"){
                val overlay = document.getElementById("overlay") as HTMLDivElement
                overlay.innerHTML = """<font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 10px; left: 15px;">YOU WERE MILKED<br>Reload the page to try again.</font>"""
              }
            }

            val collisionNormal = diff.normalize()

            position += collisionNormal * invMass / (invMass + it.invMass) * dist * 0.09f
            it.position += collisionNormal * it.invMass / (invMass + it.invMass) * dist * -0.09f
            
            val collisionTangent = Vec3(-collisionNormal.y, collisionNormal.x, 0.0f)
            val relativeVelocity = velocity - it.velocity
            
            val restitutionCoeff = 0.8f
            val impulseLength = collisionNormal.dot(relativeVelocity) / (invMass + it.invMass) * (1.0f + restitutionCoeff)
            val restitution = collisionNormal * impulseLength
            velocity -= restitution * invMass
            it.velocity += restitution * it.invMass       
          }
        }
      }
    }

    return true;
  }

  override fun move(
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>
      ) : Boolean {
    control (dt, t, keysPressed, gameObjects)

    acceleration.set(force * invMass)
    velocity += acceleration * dt
    position += velocity * dt

    angularAcceleration = torque * invAngularMass
    angularVelocity += angularAcceleration * dt
    roll += angularVelocity * dt

    velocity *= exp (-dt)
    angularVelocity *= exp (-dt)

    collision (dt, t, keysPressed, gameObjects)
    return true;
  }

}
