class Scene (
  val gl : WebGL2RenderingContext)  : UniformProvider("scene") {

  val vsTextured = Shader(gl, GL.VERTEX_SHADER, "textured-vs.glsl")
  val vsBackground = Shader(gl, GL.VERTEX_SHADER, "background-vs.glsl")  
  val fsTextured = Shader(gl, GL.FRAGMENT_SHADER, "textured-fs.glsl")
  val texturedProgram = Program(gl, vsTextured, fsTextured)
  val backgroundProgram = Program(gl, vsBackground, fsTextured)

  //TODO: create various materials with different solidColor settings
  val fighterMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/justin_avatar.png"))
  }
  val backgroundMaterial = Material(backgroundProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/heritage_background.webp"))
  }
  /*val asteroidMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/asteroid.png"))
  }*/
  val itemMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/almondmilk.png"))
  }
  val shootMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/ryan.webp"))
  }
  val bounceMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/cheese_packet_full.png"))
  }
  val speedMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/ramsey_head.png"))
  }
  val bulletMaterial = Material(texturedProgram).apply{
    this["colorTexture"]?.set(Texture2D(gl, "media/ritvik_spelling.png"))
  }

  val texturedQuadGeometry = TexturedQuadGeometry(gl)
  val backgroundMesh = Mesh(backgroundMaterial, texturedQuadGeometry)
  val fighterMesh = Mesh(fighterMaterial, texturedQuadGeometry)
  val itemMesh = Mesh(itemMaterial, texturedQuadGeometry)
  val shootMesh = Mesh(shootMaterial, texturedQuadGeometry)
  val bounceMesh = Mesh(bounceMaterial, texturedQuadGeometry)
  val speedMesh = Mesh(speedMaterial, texturedQuadGeometry)
  val bulletMesh = Mesh(bulletMaterial, texturedQuadGeometry)
  //val asteroidMesh = Mesh(asteroidMaterial, texturedQuadGeometry)
  
  val camera = OrthoCamera().apply{
    position.set(1f, 1f)
    windowSize.set(20f, 20f)
    updateViewProjMatrix()
  }

  var gameObjects = ArrayList<GameObject>()

  val avatar = object : PhysicsGameObject(fighterMesh){
    val gravity = -5.0f
    var horizantal_force = 0.0f
    var jump_force = 0.0f;
    var hit_ground = false
    var lastT = 0.0f 
    var lastTB = 0.0f
    var lastSP = 0.0f
    var enemySpeed = 0.5f
    var enemySpeedStack = 1.0f
    var enemyCount = 1
    var speed = 20.0f
    var cooldown = 0.25f
    var letGo = true
    override fun control (
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>
      ) : Boolean {

        //+ (maxOf(2*(enemyCount - 3), 0))
        if (t - lastT > 3){
        //if (t - lastT > 3){
          for (i in 1..enemyCount){
            chase(enemySpeed)
          }
          lastT = t
          enemySpeed += enemySpeedStack
          if (enemySpeed / 5 > enemyCount){
            enemyCount += 1
            //score += 25
          }
        }

        if (t - lastTB > 15){
          bounce()
          lastTB = t
        }

        if (t - lastSP > 1){
          val powerChance = Random.nextFloat() * 30

          if (powerChance <= 14.0f && powerChance >= 13.0f){
            val typeChance = Random.nextFloat() * 30
            if (typeChance < 20){
              speedPowerup(15.0f, 15.0f)
            }
            else {
              bulletPowerup(-15.0f, -15.0f)
            }

          }

          lastSP = t
        }

        if("A" in keysPressed) 
        {
          horizantal_force = -1 * speed - (speedBoost * 5.0f)
        }
        else if ("D" in keysPressed) {
          horizantal_force = speed + (speedBoost * 5.0f)
        }
        else {
          horizantal_force = 0.0f
        }

        if(("W" in keysPressed || "SPACE" in keysPressed) && position.y <= -8.0f) 
        {
          velocity.y = 50.0f
          hit_ground = false
          if (horizantal_force > 0.0f){
            torque = -30.0f
          }
          else {
            torque = 30.0f
          }
        }

        if (("O" in keysPressed || "P" in keysPressed) && letGo){
          console.log(speedBoost)
          console.log(bullets)
          letGo = false
          if (t - lastT > cooldown){
          //if (t - lastT > cooldown){
            if ("O" in keysPressed){
              for (i in 1..bullets){
                fire(-1.5f)
              }
            }
            else{
              for (i in 1..bullets){
                fire(1.5f)
              }
            }
            lastT = t
          }
        }

        if (!("P" in keysPressed || "O" in keysPressed)){
          letGo = true
        }

        if (position.y <= -8.0f){
          if (!hit_ground && jump_force < 0.0f){
            hit_ground = true 
            jump_force = 0.0f
            acceleration.y = 0.0f 
            velocity.y = 0.0f 
            position.y = -8.0f
            torque = 0.0f
            angularVelocity = 0.0f 
            angularAcceleration = 0.0f
          }
          else if (hit_ground){
            if("A" in keysPressed) 
            {
              torque = 10.0f + (speedBoost * 2.5f)
            }
            else if("D" in keysPressed) 
            {
              torque = -10.0f - (speedBoost * 2.5f)
            }
            else {
              torque = 0.0f
            }
          }
        }
        else{
          jump_force += gravity
        }

        if (position.y <= -8.001f){
          position.y = -8.0f
        }

        if (position.x > 124.0f){
          position.x = 124.0f
        }
        else if (position.x < -124.5f){
          position.x = -124.5f
        }

        var walk = Vec3(horizantal_force, jump_force, 0.0f)
        force.set(walk)

        var speedDisp = speedBoost + 1

        val overlay = document.getElementById("overlay") as HTMLDivElement
        overlay.innerHTML = """<font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 10px; left: 15px;">Round: $enemyCount<br></font>
                               <font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 110px; right: 15px;">$health </font>
                               <font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 60px; right: 15px;">Speed: $speedDisp</font>
                               <font color="red" style="font-size: 36px; font-family: 'Comic Sans MS', sans-serif; position: fixed; bottom: 10px; right: 15px;">Multishot x$bullets</font>"""

        return true
      }
  }
  init {
    gameObjects += GameObject(backgroundMesh)
    gameObjects += avatar
    avatar.roll = 1f
    avatar.health = 3
    avatar.damage = 1
    avatar.name = "justin"
    chase(0.0f)
    bounce()
    bounce()
  }

  
  fun chase(speedBoost : Float){
    val item = object : PhysicsGameObject(itemMesh){
    //var forceX = Random.nextFloat() * 30 - 15
    //var forceY = Random.nextFloat() * 30 - 15
    var forceX = 0.0f
    var forceY = 0.0f
    var chaseSpeed = 20.0f + speedBoost
    var lastRoll = 0.0f
    override fun control (
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>
      ) : Boolean {

        val directionX = avatar.position.x - position.x
        val directionY = avatar.position.y - position.y

        
        val distance = sqrt((directionX * directionX + directionY * directionY))

       
        if (distance > 0) { 
            forceX = (directionX / distance) * chaseSpeed
            forceY = (directionY / distance) * chaseSpeed
        }

        force.set(Vec3(forceX, forceY, 0.0f))
        roll = atan(forceY/forceX)
        //roll = (atan(forceY/forceX) + lastRoll) / 2
        //lastRoll = atan(forceY/forceX)

        if (position.y < -8.0f){
            forceY *= -1.0f
            velocity.y *= -1.0f
        }
        else if (position.y > 10.0f){
            forceY *= -1.0f
            velocity.y *= -1.0f
        }

        /*if (position.x > 17.0f){
          forceX *= -1.0f
          velocity.x *= -1.0f
        }
        else if (position.x < -16.5f){
          forceX *= -1.0f
          velocity.x *= -1.0f
        }*/

        if (position.y < -8.05f){
          position.y = -8.0f
        }
        if (position.y > 10.05f){
          position.y = 10.0f
        }

        if (position.x > 124.0f){
          position.x = 124.0f
        }
        else if (position.x < -124.5f){
          position.x = -124.5f
        }

        val move = Vec3(forceX, forceY, 0.0f)
        force.set(move)

        return true
      }
    }
  
    val initPos = Random.nextFloat() * 32 - 17
    console.log("just added")
    gameObjects += item
    item.position.set(Vec3(initPos, 9.0f, 0.0f))
    item.health = 3
    item.damage = 1
    item.name = "milk"
  }

