      ) : Boolean {

        // Set the calculated force
        force.set(Vec3(forceX, forceY, 0.0f))

        if (position.y < -8.0f){
            forceY *= -1.0f
            velocity.y *= -1.0f
        }
        else if (position.y > 10.0f){
            forceY *= -1.0f
            velocity.y *= -1.0f
        }

        /*if (position.x > 17.0f){
          forceX *= -1.0f
          velocity.x *= -1.0f
        }
        else if (position.x < -16.5f){
          forceX *= -1.0f
          velocity.x *= -1.0f
        }*/

        if (position.y < -8.05f){
          position.y = -8.0f
        }
        if (position.y > 10.05f){
          position.y = 10.0f
        }

        if (position.x > 124.0f){
          position.x = 124.0f
        }
        else if (position.x < -124.5f){
          position.x = -124.5f
        }

        val move = Vec3(forceX, forceY, 0.0f)
        force.set(move)

        return true
      }
    }
  
    val initPos = (Random.nextFloat() - 0.5f) * 30 + avatar.position.x 
    console.log("just added")
    gameObjects += b
    b.position.set(Vec3(initPos, 0.0f, 0.0f))
    b.health = 3
    b.damage = -1
    b.name = "cheese"
  }

