  fun fire(direc : Float){
    val missile = object : PhysicsGameObject(shootMesh){
      override fun control (
      dt : Float,
      t : Float,
      keysPressed : Set<String>,
      gameObjects : List<GameObject>
      ) : Boolean {
        var f = Vec3(50.0f, 0.0f, 0.0f)
        if (direc < 0){
          f = Vec3(-50.0f, 0.0f, 0.0f)
        }
        force.set(f)


        //avoid lag
        if (position.x > 154.0f){
          health = 0
        }
        else if (position.x < -154.5f){
          health = 0
        }

        return true
      }
    }

    missile.position.set(Vec3(avatar.position.x + direc, avatar.position.y, 0.0f))
    if (direc > 0){
      missile.velocity.set(Vec3(abs(avatar.velocity.x) + 25.0f, 0.0f, 0.0f))
    }
    else{
      missile.velocity.set(Vec3(-1 * abs(avatar.velocity.x) - 25.0f, 0.0f, 0.0f))
    }
    
    missile.damage = 3 
    missile.name = "missile"
    gameObjects += missile
  }

  fun resize(canvas : HTMLCanvasElement) {
    gl.viewport(0, 0, canvas.width, canvas.height)//#viewport# tell the rasterizer which part of the canvas to draw to HUN a raszterizl ide rajzoljon
    camera.setAspectRatio(canvas.width.toFloat()/canvas.height)
  }

  val timeAtFirstFrame = Date().getTime()
  var timeAtLastFrame =  timeAtFirstFrame
  //TODO: add property reflecting uniform scene.time
  //TODO: add all programs as child components

  @Suppress("UNUSED_PARAMETER")
  fun update(keysPressed : Set<String>) {
    val timeAtThisFrame = Date().getTime() 
    val dt = (timeAtThisFrame - timeAtLastFrame).toFloat() / 1000.0f
    val t = (timeAtThisFrame - timeAtFirstFrame).toFloat() / 1000.0f
    //TODO: set property time (reflecting uniform scene.time) 
    timeAtLastFrame = timeAtThisFrame
    
    camera.position.set(Vec3(avatar.position.x, 0.0f, 0.0f))
    camera.updateViewProjMatrix()

    gl.clearColor(0.3f, 0.0f, 0.3f, 1.0f)//## red, green, blue, alpha in [0, 1]
    gl.clearDepth(1.0f)//## will be useful in 3D HUN 3D-ben lesz hasznos
    gl.clear(GL.COLOR_BUFFER_BIT or GL.DEPTH_BUFFER_BIT)//#or# bitwise OR of flags

    gl.enable(GL.BLEND)
    gl.blendFunc(
      GL.SRC_ALPHA,
      GL.ONE_MINUS_SRC_ALPHA)

    gameObjects.forEach{
      it.move(dt, t, keysPressed, gameObjects)
    }
    gameObjects.forEach{
      it.update()
      if (it is PhysicsGameObject && it.alive == false){
        console.log("remove")
        gameObjects.remove(it)
      }
    }
    gameObjects.forEach{
      it.draw(this, camera)
    }
  }
}
